n;\n        transition: all 0.6s;\n\n        &:hover {\n            transform: scale(1.1);\n        }\n    }\n\n    &__level-btn {\n        position: relative;\n        font-size: 16px;\n        padding: 15px 5px;\n        min-width: 115px;\n        cursor: pointer;\n        transition: all 0.6s;\n        filter: drop-shadow(2px 2px 2px rgb(137, 137, 137));\n\n        &.selected {\n            cursor: auto;\n            background: rgb(219, 219, 219);\n            transform: translateY(-10px);\n            filter: drop-shadow(0px 3px 3px rgb(0, 0, 0));\n            &:hover {\n                background: rgb(219, 219, 219);\n            }\n        }\n\n        &::after {\n            content: '';\n            display: block;\n            position: absolute;\n            left: 0;\n            bottom: -20px;\n            width: 100%;\n            height: 9px;\n            border: 1px solid #c0c0c0;\n            font-size: 8px;\n            line-height: 8px;\n            text-align: left;\n        }\n    }\n}\n\n.levels-list {\n    &__item {\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n        justify-content: flex-start;\n        gap: 5px;\n        padding: 5px 5px;\n        border: 1px solid #6a6a6a;\n        border-radius: 4px;\n        font-size: 14px;\n        font-style: italic;\n        user-select: none;\n        text-align: center;\n        cursor: pointer;\n\n        &-img {\n            height: auto;\n            width: 170px;\n            filter: blur(2px);\n            transition: all 0.6s;\n        }\n\n        &.completed {\n            outline: 2px solid green;\n            background-color: #d4ffd7;\n            .levels-list__item-img {\n                filter: none;\n            }\n        }\n    }\n}\n\n@media (hover: hover) and (pointer: fine) {\n    .levels-list__item:hover .levels-list__item-img {\n        transform: scale(1.03);\n    }\n}\n\n.list-item__img-wrapper {\n    height: 96px;\n    width: 170px;\